.editor-preference-modal {
  max-width: 90vw;
  width: 800px;
}
.editor-preference-modal .left-aligned {
  font-size: 1.05rem;
  font-weight: 300;
  text-align: left;
}
.editor-preference-modal p {
  font-size: 1.05rem;
}
.editor-preference-modal ul {
  list-style-type: disc;
  margin-top: -0.75em;
  padding-left: 1em;
}
.editor-preference-modal ul > li {
  margin-bottom: 0.5em;
}
.editor-preference-modal__textarea {
  background-color: var(--everyday-blue-100);
  border: 0;
  border-radius: 0;
  margin: 2em 0;
  min-height: 200px;
  padding: 1em;
  width: calc(100% - 2.1em);
}
.editor-preference-modal__textarea:focus {
  border-color: var(--wp-admin-theme-color);
}
.editor-preference-modal__footer {
  display: flex;
  justify-content: flex-end;
}
.editor-preference-modal__footer__button--back {
  margin-right: auto !important;
}
.editor-preference-modal__footer__button--skip, .editor-preference-modal__footer__button--next {
  margin-right: 1em !important;
}
.editor-preference-modal__redirecting > svg {
  stroke: #555;
  stroke-width: 0.5;
  width: 250px;
}
.editor-preference-modal__redirecting > svg > path {
  animation: animateWordPressSVG 2s ease infinite alternate;
  stroke-dasharray: 300;
  stroke-dashoffset: 300;
}
.editor-preference-modal__redirecting p {
  font-size: 1.5rem;
  font-weight: 300;
  margin-top: 0;
  padding: 0 30% 1em;
}
.editor-preference-modal__content-contact .nextgen-modal__title {
  margin-bottom: 2rem;
  position: relative;
}
.editor-preference-modal__content-contact .nextgen-modal__title > span {
  display: inline-block;
  position: relative;
}
.editor-preference-modal__content-contact .nextgen-modal__title > span::after {
  background-image: linear-gradient(to right, #1976d2, #9e42b5);
  bottom: -8px;
  content: "";
  display: block;
  height: 2px;
  left: 1px;
  position: absolute;
  right: 1px;
}
.editor-preference-modal__content-contact .nextgen-modal__subtitle {
  margin: 1rem 0 2rem;
}
.editor-preference-modal__content-contact > button {
  background: none;
  border: 0;
}
.editor-preference-modal .buttons-bar {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 2.5em 0;
}
.editor-preference-modal .buttons-bar__button {
  align-items: center;
  background: none;
  border: 1px solid var(--wp-admin-theme-color);
  border-radius: 8px;
  display: flex;
  fill: currentColor;
  flex-direction: column;
  font-size: 1.1rem;
  font-weight: 300;
  justify-content: flex-start;
  margin: 0 1em;
  padding: 2em;
  transition: background-color 0.2s;
  width: 200px;
}
.editor-preference-modal .buttons-bar__button:hover {
  cursor: pointer;
}
.editor-preference-modal .buttons-bar__button.is-selected {
  background-color: var(--wp-admin-theme-color);
  color: #fff;
}
.editor-preference-modal .buttons-bar__button > svg {
  margin-bottom: 0.5em;
}

@keyframes animateWordPressSVG {
  0% {
    stroke-dashoffset: 0;
  }
}